import React from "react";
import { Carousel } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.css";
import "./App.css";

export default function App() {
  return (
    <div>
      <h2>My Games</h2>
      <p>
        This slider contains all my games. All the games are PC web games.
        You'll need a PC to play them.
      </p>
      <Carousel>
        <Carousel.Item>
          <div>
            <h3>Box Shooter</h3>
            <p>
              Shoot the boxes as they appear before the timer runs out. Collect
              enough points to get to the next level.
            </p>
            <p>
              Click <a href="./MyGames/BoxShooter">here</a> to go to the game
              page.
            </p>
          </div>
        </Carousel.Item>

        <Carousel.Item>
          <div>
            <h3>Roller Madness</h3>
            <p>
              My first ever <i>game</i> game. That is, a game in a traditional
              sense.
            </p>
            <p>
              Use the ball to collect coins. Collect enough coins to get to the
              next level. There are only 2 levels.
            </p>
            <p>
              Click <a href="./MyGames/RollerMadness/">here</a> to go to the
              game page.
            </p>
          </div>
        </Carousel.Item>

        <Carousel.Item>
          <h3>Solar Sytem Simulation</h3>
          <p>
            The first project I ever created with Unity3D. It is an interactive
            model of the solar system with as accurate as possible relative
            speeds of rotation and revolution.
          </p>
          <p>
            Click <a href="./MyGames/SolarSystemSimulation/">here</a> to go to
            the game page.
          </p>
        </Carousel.Item>

        <Carousel.Item>
          <div>
            <h3>Back Home</h3>
            <p>
              A game I made during the Global Game Jam 2020 in 48 hours. My
              first game that I made completely from scratch.
            </p>
            <p>
              Click <a href="./MyGames/BackHome">here</a> to play Back Home.
            </p>
          </div>
        </Carousel.Item>
      </Carousel>
    </div>
  );
}
